import {React, useState} from 'react'
import {CRow, CCol, CFormGroup, CLabel, CInput, CTextarea, CTooltip} from '@coreui/react'
import Select from 'react-select'
import AsyncSelect from 'react-select/async';
import supabase from 'src/supabase';
import Swal from 'sweetalert2'
import { useDispatch, useSelector } from 'react-redux';

const DataUmum = () =>{
    const value = useSelector((state)=>state.valBarang)
    const dispatch = useDispatch()
    const handleClickSelect = (e, index) =>{
        if(e.value == 'ikan'){
            Swal.fire({
                icon: 'warning',
                title: 'Perhatian',
                text: `HS yang anda pilih terkena karantina Ikan. Harap Isikan Data Khusus Ikan`
            })   
            dispatch({type:'set',pilihan:3}) 
            dispatch({type:'set', valBarang:{...value,['barantanFlag']:false, ['barantanDisabled']:true, [index.name]:e}}) 
            console.log(value.hscode)       
        } else if(e.value == 'nonikan'){
            dispatch({type:'set', pilihan:0, valBarang:{...value, ['barantanDisabled']:false, [index.name]:e}})
            console.log(value.hscode)             
        }   
    }
    const handleCheck = (e) =>{
        if(e.target.checked){
            dispatch({type:'set', pilihan:2})
            Swal.fire({
                icon: 'warning',
                title: 'Perhatian',
                text: `Harap Isikan Data Khusus Barantan`
            })             
        } else {
            dispatch({type:'set', pilihan:0})            
        }
        dispatch({type:'set',valBarang:{...value,[e.target.name]:e.target.checked}}) 
    }
    const handleLoad = async (e) =>{
        return new Promise((resolve, reject)=>{
            setTimeout(() => {
                resolve([
                    {
                        'label':'03011910',
                        'value':'ikan'
                    },
                    {
                        'label':'contoh hs nonlartas',
                        'value':'nonikan'
                    }                    
                ])
              }, 1000)            
        })     
    }
    return(
        <>
            <div className="card px-3 py-3" style={{borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
                <div className="d-flex flex-row justify-content-between">
                <h5>Data Barang</h5>
                <div className="d-flex flex-row">
                    <div className="d-flex flex-row align-items-center mr-2">
                        <input name="barantanFlag" disabled={value.barantanDisabled} checked={value.barantanFlag} onChange={(e)=>handleCheck(e)} type="checkbox"/>                        
                    </div>
                    <div className="d-flex flex-row align-items-center">
                        Termasuk Barang Barantan                      
                    </div>                                        
                </div>
                </div>
                <hr/>                            
                <CRow>
                    <CCol md="3">
                        <CFormGroup>
                            <CLabel>HS Code
                                <CTooltip content="Mockup Note :<br>Contoh HS Lartas<br>Ikan : 03011910">
                                <i class="fas fa-question-circle ml-2"></i>                               
                                </CTooltip>   
                            </CLabel>                         
                            <AsyncSelect name="hscode" value={value.hscode} noOptionsMessage={({inputValue}) => !inputValue ? "Mohon Ketik Beberapa Digit" : "Tidak ada"} onChange={(index, e)=>handleClickSelect(index, e)} loadOptions={(e)=>handleLoad(e)}/>
                        </CFormGroup>
                    </CCol>
                    <CCol md="9">
                        <CFormGroup>
                            <CLabel>Uraian HS Code</CLabel>
                            <CTextarea value={value.hscode ? value.hscode.ur_brg_skep : ""} placeholder="Please Select HS Code" disabled/>
                        </CFormGroup>
                    </CCol>                                
                </CRow>
                <CRow>
                    <CCol md="12">
                        <CFormGroup>
                            <CLabel>Uraian</CLabel>
                            <CTextarea placeholder="Uraian"/>
                        </CFormGroup>
                    </CCol>
                </CRow>
                <CRow>
                    <CCol md="3">
                        <CFormGroup>
                            <CLabel>Merk</CLabel>
                            <CInput placeholder="Merk"/>
                        </CFormGroup>
                    </CCol>
                    <CCol md="3">
                        <CFormGroup>
                            <CLabel>Tipe</CLabel>
                            <CInput placeholder="Tipe"/>
                        </CFormGroup>
                    </CCol>   
                    <CCol md="3">
                        <CFormGroup>
                            <CLabel>Ukuran</CLabel>
                            <CInput placeholder="Ukuran"/>
                        </CFormGroup>
                    </CCol> 
                    <CCol md="3">
                        <CFormGroup>
                            <CLabel>Spesifikasi Lain</CLabel>
                            <CInput placeholder="Spesifikasi Lain"/>
                        </CFormGroup>
                    </CCol>                                                        
                </CRow>
                <CRow>
                    <CCol md="3">
                        <CFormGroup>
                            <CLabel>Kode Barang</CLabel>
                            <CInput placeholder="Kode Barang"/>
                        </CFormGroup>
                    </CCol>
                    <CCol md="3">
                        <CFormGroup>
                            <CLabel>Kondisi Barang</CLabel>
                            <Select/>
                        </CFormGroup>
                    </CCol>                    
                    <CCol md="3">
                        <CFormGroup>
                            <CLabel>Negara Asal</CLabel>
                            <Select/>
                        </CFormGroup>
                    </CCol>
                    <CCol md="3">
                        <CFormGroup>
                            <CLabel>Daerah Asal</CLabel>
                            <Select/>
                        </CFormGroup>
                    </CCol>                                        
                </CRow>
                <CRow>
                    <CCol md="6">
                        <h5>Harga</h5>
                        <hr/>
                        <CRow>
                            <CCol md="12">
                                <CFormGroup>
                                    <CLabel>Harga FOB</CLabel>
                                    <CInput placeholder="Harga FOB" type="number"/>
                                </CFormGroup>
                            </CCol>
                        </CRow>
                        <CRow>
                            <CCol md="12">
                                <CFormGroup>
                                    <CLabel>Jumlah Satuan</CLabel>
                                    <CInput placeholder="Jumlah Satuan" type="number"/>
                                </CFormGroup>
                            </CCol>                                                      
                        </CRow> 
                        <CRow>
                            <CCol md="12">
                                <CFormGroup>
                                    <CLabel>Kode Satuan</CLabel>
                                    <Select/>
                                </CFormGroup>
                            </CCol>                              
                        </CRow>
                        <CRow>
                            <CCol md="12">
                                <CFormGroup>
                                    <CLabel>Harga Satuan FOB</CLabel>
                                    <CInput type="number" disabled/>
                                </CFormGroup>
                            </CCol>
                        </CRow>                                               
                    </CCol>
                    <CCol md="6">
                        <h5>Kemasan</h5>
                        <hr/>
                        <CRow>
                            <CCol md="6">
                                <CFormGroup>
                                    <CLabel>Jenis Kemasan</CLabel>
                                    <Select/>
                                </CFormGroup>
                            </CCol>
                            <CCol md="6">
                                <CFormGroup>
                                    <CLabel>Jumlah Kemasan</CLabel>
                                    <CInput placeholder="Jumlah Kemasan" type="number"/>
                                </CFormGroup>
                            </CCol>                            
                        </CRow>                        
                        <CRow>
                            <CCol md="9">
                                <CFormGroup>
                                    <CLabel>Netto</CLabel>
                                    <CInput placeholder="Netto" type="number"/>
                                </CFormGroup>
                            </CCol>
                            <CCol md="3">
                                <CFormGroup>
                                    <CLabel>Satuan</CLabel>
                                    <CInput placeholder="Satuan" value="KGM" disabled/>
                                </CFormGroup>
                            </CCol>                            
                        </CRow>
                        <CRow>
                            <CCol md="9">
                                <CFormGroup>
                                    <CLabel>Bruto</CLabel>
                                    <CInput placeholder="Bruto" type="number"/>
                                </CFormGroup>
                            </CCol>
                            <CCol md="3">
                                <CFormGroup>
                                    <CLabel>Satuan</CLabel>
                                    <CInput placeholder="Satuan" value="KGM" disabled/>
                                </CFormGroup>
                            </CCol>                            
                        </CRow> 
                        <CRow>
                            <CCol md="9">
                                <CFormGroup>
                                    <CLabel>Volume</CLabel>
                                    <CInput placeholder="Volume" type="number"/>
                                </CFormGroup>
                            </CCol>
                            <CCol md="3">
                                <CFormGroup>
                                    <CLabel>Satuan</CLabel>
                                    <CInput placeholder="Satuan" value="M3" disabled/>
                                </CFormGroup>
                            </CCol>                            
                        </CRow>                                                                      
                    </CCol>                    
                </CRow>
                {/* <CRow>
                    <CCol md="4">
                        <CFormGroup>
                            <CLabel>Negara Asal Barang</CLabel>
                            <Select/>
                        </CFormGroup>
                    </CCol>
                    <CCol md="4">
                        <CFormGroup>
                            <CLabel>Berat Kotor</CLabel>
                            <CInput placeholder="Berat Kotor" type="number"/>
                        </CFormGroup>
                    </CCol>
                    <CCol md="4">
                        <CFormGroup>
                            <CLabel>Volume</CLabel>
                            <CInput placeholder="Volume" type="number"/>
                        </CFormGroup>
                    </CCol>                                
                </CRow>
                <CRow>
                    <CCol md="4">
                        <CFormGroup>
                            <CLabel>Netto</CLabel>
                            <CInput placeholder="Netto" type="number"/>
                        </CFormGroup>
                    </CCol>
                    <CCol md="4">
                        <CFormGroup>
                            <CLabel>Satuan Netto</CLabel>
                            <Select/>
                        </CFormGroup>
                    </CCol>
                    <CCol md="4">
                        <CFormGroup>
                            <CLabel>FOB</CLabel>
                            <Select/>
                        </CFormGroup>
                    </CCol>                                
                </CRow>  
                <CRow>
                    <CCol md="3">
                        <CFormGroup>
                            <CLabel>Valuta</CLabel>
                            <Select/>
                        </CFormGroup>
                    </CCol>
                    <CCol md="9">
                        <CFormGroup>
                            <CLabel>Nilai FOB</CLabel>
                            <CInput placeholder="Nilai FOB" type="number"/>
                        </CFormGroup>
                    </CCol>                                
                </CRow>                           */}
            </div> 
        </>      
    )
}

export default DataUmum