import { CModal, CModalBody } from '@coreui/react'
import {React, useEffect, useState} from 'react'
import { useDispatch, useSelector } from 'react-redux'
import {ModalOCRAwal, ModalOCRTengah} from './bridge'

const ModalOCR = () =>{
    const modal = useSelector((state)=>state.modal)
    const pageocr = useSelector((state)=>state.pageocr)
    const dispatch = useDispatch()
    return(
        <CModal onClose={()=>{
            dispatch({type:'set', modal:false})
            dispatch({type:'set', pageocr:1})
        }} className="modalocr" size="xl" show={modal} style={{borderRadius:'20px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
            <CModalBody>
                {
                    pageocr == 1 ?
                    <ModalOCRAwal/>
                    :
                    pageocr == 2 ? 
                    <ModalOCRTengah/>
                    :
                    <></>
                }
            </CModalBody>
        </CModal>
    )
}

export default ModalOCR