import { CFormGroup, CLabel, CInput, CCol, CRow} from '@coreui/react'
import {React} from 'react'
import Select from 'react-select'

const DataIkan = () =>{
    return(
        <div className="card px-3 py-3" style={{borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
            <h5>Data Ikan</h5>
            <hr/>
            <CRow>
                <CCol md="4">
                    <CFormGroup>
                        <CLabel>ID Ikan</CLabel>
                        <Select/>
                    </CFormGroup>
                </CCol>
                <CCol md="4">
                    <CFormGroup>
                        <CLabel>Nama Lokal</CLabel>
                        <CInput disabled placeholder="Please Select ID Ikan"/>
                    </CFormGroup>
                </CCol>
                <CCol md="4">
                    <CFormGroup>
                        <CLabel>Nama Umum</CLabel>
                        <CInput disabled placeholder="Please Select ID Ikan"/>
                    </CFormGroup>
                </CCol>                                
            </CRow>
            <CRow>
                <CCol md="4">
                    <CFormGroup>
                        <CLabel>Nama Latin</CLabel>
                        <CInput disabled placeholder="Please Select ID Ikan"/>
                    </CFormGroup>
                </CCol>
                <CCol md="4">
                    <CFormGroup>
                        <CLabel>Kode Tarif</CLabel>
                        <CInput disabled placeholder="Please Select ID Ikan"/>
                    </CFormGroup>
                </CCol>
                <CCol md="4">
                    <CFormGroup>
                        <CLabel>Bentuk</CLabel>
                        <CInput disabled placeholder="Please Select ID Ikan"/>
                    </CFormGroup>
                </CCol>                                
            </CRow>
            {/* <CRow>
                <CCol md="4">
                    <CFormGroup>
                        <CLabel>Ukuran</CLabel>
                        <CInput placeholder="Ukuran"/>
                    </CFormGroup>
                </CCol>
                <CCol md="4">
                    <CFormGroup>
                        <CLabel>Jumlah Satuan</CLabel>
                        <CInput placeholder="Jumlah Satuan"/>
                    </CFormGroup>
                </CCol>
                <CCol md="4">
                    <CFormGroup>
                        <CLabel>Kode Satuan</CLabel>
                        <CInput disabled placeholder="Please Select ID Ikan"/>
                    </CFormGroup>
                </CCol>                                
            </CRow>  */}
            <CRow>
                <CCol md="6">
                    <CFormGroup>
                        <CLabel>Harga Satuan</CLabel>
                        <CInput disabled placeholder="Please Select ID Ikan"/>
                    </CFormGroup>
                </CCol>
                <CCol md="6">
                    <CFormGroup>
                        <CLabel>Nomor PI</CLabel>
                        <CInput placeholder="Nomor PI"/>
                    </CFormGroup>
                </CCol>                              
            </CRow>                                   
        </div>
    )
}

export default DataIkan