import { CButton } from '@coreui/react'
import {React, useEffect, useState} from 'react'
import { useDispatch, useSelector } from 'react-redux'
import {Perusahaan, Pengangkutan, Barang,LogoPerusahaan, LogoPengangkutan, LogoBarang, LogoLayanan, DataLayanan, ModalOCR} from './bridge'

const LamanForm = () =>{
    const datasupabase = useSelector((state)=>state.datasupabase)
    const [modal, setModal] = useState(true)
    const tabtrue = useSelector((state)=>state.tabtrue)
    const dispatch = useDispatch()
    const [page, setPage] = useState("Perusahaan")
    const Page = {
        "Perusahaan":<Perusahaan/>,
        "Pengangkutan":<Pengangkutan/>,
        "Barang":<Barang/>,
        "Layanan":<DataLayanan/>
    }
    const handleChangePage = (e) =>{
        setPage(e)
    }
    useEffect(()=>{
        dispatch({type:'set', modal:true})
    },[])
    const nella = useSelector((state)=>state.nella)
    return(
        <>
        <ModalOCR/>
        <div className="card px-5 pb-5" style={{borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
            <div className="d-flex flex-row justify-content-between">
                <div className="d-flex flex-row mb-5 mt-3">
                    <div className="mr-2">Nomor Pengajuan : <b>{datasupabase.no_aju}</b> |</div>
                    <div className="mr-2">KSWP : <b>Valid</b> |</div>
                    <div className="mr-2">Jenis API : <b>API-P</b> |</div>  
                    {/* <div className="mr-2">Jenis Pengajuan : <b>{nella == 'X' ? "Pabean dan/atau Karantina":"Pabean dan/atau Karantina dan SKA"}</b></div>                                 */}
                </div>
                <div className="d-flex flex-row mb-5 mt-3">
                    <CButton onClick={()=>{
                        dispatch({type: 'set', modal: true })
                    }} style={{borderRadius:'12px'}} className="btn btn-light btn-sm"><i class="fas fa-magic"></i> OCR</CButton>
                </div>                
            </div>
            <div className="d-flex flex-row justify-content-center mb-3">
                {
                    tabtrue ?
                    <>
                        <div className={
                                page == "Perusahaan" ?
                                "d-flex flex-column mr-5 on"
                                :
                                "d-flex flex-column mr-5 off"
                            } style={{minWidth:'9.5em'}}>
                                <div role="button" id="Perusahaan" className="mb-1 px-2 py-2" onClick={(e)=>handleChangePage("Perusahaan")} style={{borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
                                    <div id="Perusahaan" className="d-flex justify-content-center">
                                        <LogoPerusahaan id="Perusahaan" style={{width:'80%'}} />
                                        <div className="d-flex flex-row">                                
                                        </div>
                                    </div>
                                </div>  
                                <div className="d-flex flex-row justify-content-center" style={
                                    page == "Perusahaan" ?
                                    {
                                        fontWeight:'bold',
                                        color:'#02275d'
                                    }
                                    :
                                    {
                                        fontWeight:'normal',
                                        color:'#02275d'
                                    }                        
                                }>
                                    Data Perusahaan
                                </div>                     
                            </div>  
                            <div className={
                                page == "Pengangkutan" ?
                                "d-flex flex-column mr-5 on"
                                :
                                "d-flex flex-column mr-5 off"
                            } style={{minWidth:'9.5em'}}>
                                <div role="button" id="Pengangkutan" className="mb-1 px-2 py-2" onClick={(e)=>handleChangePage("Pengangkutan")} style={{borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
                                    <div id="Pengangkutan" className="d-flex justify-content-center">
                                        <LogoPengangkutan id="Pengangkutan" style={{width:'80%'}} />
                                        <div className="d-flex flex-row">                                
                                        </div>
                                    </div>
                                </div>  
                                <div className="d-flex flex-row justify-content-center" style={
                                    page == "Pengangkutan" ?
                                    {
                                        fontWeight:'bold',
                                        color:'#02275d'
                                    }
                                    :
                                    {
                                        fontWeight:'normal',
                                        color:'#02275d'
                                    }                        
                                }>
                                    Data Transportasi
                                </div>                     
                            </div> 
                            <div className={
                                page == "Barang" ?
                                "d-flex flex-column mr-5 on"
                                :
                                "d-flex flex-column mr-5 off"
                            } style={{minWidth:'9.5em'}}>
                                <div role="button" id="Barang" className="mb-1 px-2 py-2" onClick={(e)=>handleChangePage("Barang")} style={{borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
                                    <div id="Barang" className="d-flex justify-content-center">
                                        <LogoBarang id="Barang" style={{width:'80%'}} />
                                        <div className="d-flex flex-row">                                
                                        </div>
                                    </div>
                                </div>  
                                <div className="d-flex flex-row justify-content-center" style={
                                    page == "Barang" ?
                                    {
                                        fontWeight:'bold',
                                        color:'#02275d'
                                    }
                                    :
                                    {
                                        fontWeight:'normal',
                                        color:'#02275d'
                                    }                        
                                }>
                                    Data Barang
                                </div>                     
                            </div>   
                            <div className={
                                page == "Layanan" ?
                                "d-flex flex-column mr-5 on"
                                :
                                "d-flex flex-column mr-5 off"
                            } style={{minWidth:'9.5em'}}>
                                <div role="button" id="Layanan" className="mb-1 px-2 py-2" onClick={(e)=>handleChangePage("Layanan")} style={{borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
                                    <div id="Layanan" className="d-flex justify-content-center">
                                        <LogoLayanan id="Layanan" style={{width:'80%'}} />
                                        <div className="d-flex flex-row">                                
                                        </div>
                                    </div>
                                </div>  
                                <div className="d-flex flex-row justify-content-center" style={
                                    page == "Layanan" ?
                                    {
                                        fontWeight:'bold',
                                        color:'#02275d'
                                    }
                                    :
                                    {
                                        fontWeight:'normal',
                                        color:'#02275d'
                                    }                        
                                }>
                                    Data Layanan
                                </div>                     
                            </div>                      
                    </>
                    :
                    <>  
                <div className={
                    page == "Layanan" ?
                    "d-flex flex-column mr-5 on"
                    :
                    "d-flex flex-column mr-5 off"
                } style={{minWidth:'9.5em'}}>
                    <div role="button" id="Layanan" className="mb-1 px-2 py-2" onClick={(e)=>handleChangePage("Layanan")} style={{borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
                        <div id="Layanan" className="d-flex justify-content-center">
                            <LogoLayanan id="Layanan" style={{width:'80%'}} />
                            <div className="d-flex flex-row">                                
                            </div>
                        </div>
                    </div>  
                    <div className="d-flex flex-row justify-content-center" style={
                        page == "Layanan" ?
                        {
                            fontWeight:'bold',
                            color:'#02275d'
                        }
                        :
                        {
                            fontWeight:'normal',
                            color:'#02275d'
                        }                        
                    }>
                        Data Layanan
                    </div>                     
                </div>                      
                    </>                    
                }                                                                                                                                            
            </div>
            <div className="card px-4 py-4" style={{borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
                {Page[page]}
            </div>
        </div>
        </>
    )
}

export default LamanForm