import {React} from 'react'
import {CRow, CCol, CFormGroup, CLabel, CInput} from '@coreui/react'
import Select from 'react-select'

const DataHewan = () =>{
    return(
        <div className="card px-3 py-3" style={{borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
            <h5>Data Hewan</h5>
            <hr/>
            <CRow>
                <CCol md="4">
                    <CFormGroup>
                        <CLabel>Klasifikasi</CLabel>
                        <Select
                        options={[
                            {label:'Hewan Hidup', value:'1'},
                            {label:'BAH', value:'2'},
                            {label:'HBAH', value:'3'},
                            {label:'Benda Lain', value:'4'}
                        ]}
                        />
                    </CFormGroup>
                </CCol>
                <CCol md="4">
                    <CFormGroup>
                        <CLabel>Komoditi</CLabel>
                        <Select
                        />
                    </CFormGroup>
                </CCol>
                <CCol md="4">
                    <CFormGroup>
                        <CLabel>Breed</CLabel>
                        <CInput
                        />
                    </CFormGroup>
                </CCol>                                
            </CRow>
            <CRow>
                <CCol md="6">
                    <CFormGroup>
                        <CLabel>Nama Umum</CLabel>
                        <CInput placeholder="Nama Umum"/>
                    </CFormGroup>
                </CCol>   
                <CCol md="6">
                    <CFormGroup>
                        <CLabel>Nama Latin</CLabel>
                        <CInput placeholder="Nama Latin"/>                        
                    </CFormGroup>
                </CCol>                  
            </CRow>             
        </div>
    )
}

export default DataHewan