import { CFormGroup, CLabel, CModal, CModalBody, CModalHeader, CRow, CCol, CModalFooter, CButton, CTextarea, CInput} from '@coreui/react'
import {React, useEffect, useState} from 'react'
import { useDispatch, useSelector } from 'react-redux'
import Select from 'react-select'
import {DataUmum, DataHewan, DataTumbuhan, DataIkan, BeaKeluar} from './bridge'

const ModalBarang = (props) =>{
    const dispatch = useDispatch()
    const pilihan = useSelector((state)=>state.pilihan)
    const [tab, setTab] = useState(<DataUmum/>)
    const [value, setValue] = useState(1)
    const [modal, setModal] = useState(false)
    useEffect(()=>{
        setModal(props.modal)
    },[props.modal])
    const arrKategoriBarang = {
        1:[
            {
                value:2,
                label:"Data Hewan Non Ikan",
                object:<DataHewan/>
            }            
        ],
        2:[
            {
                value:3,
                label:"Data Tumbuhan [Barantan]",
                object:<DataTumbuhan/>
            },
            {
                value:2,
                label:"Data Hewan [Barantan]",
                object:<DataHewan/>
            }                                   
        ],
        3:[
            {
                value:4,
                label:"Data Ikan",
                object:<DataIkan/>
            }            
        ],
        0:[]                      
    }
    return(
        <CModal
        show={modal}
        size="xl"
        onClose={()=>{
            setModal(false)
            props.callback(false)
            dispatch({type:'set',valBarang:{['hscode']:null,['barantanFlag']:false,['barantanDisabled']:false},['pilihan']:0})
        }}
        >
            <CModalHeader closeButton><h5>Tambah Barang</h5></CModalHeader>
            <CModalBody>              
                <div className="card px-3 py-3" style={{borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
                    <CRow>
                        <CCol md="3">
                            <div className="card px-3 py-3" style={{borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px', height:'100%'}}>
                                <div onClick={()=>{
                                        setTab(<DataUmum/>)
                                        setValue(1)
                                    }} role="button" className={
                                        value == 1 ?
                                        "card px-2 py-2 diklik"
                                        :
                                        "card px-2 py-2 notklik"
                                    } style={{borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
                                        Data Umum
                                </div>  
                                <div onClick={()=>{
                                        setTab(<BeaKeluar/>)
                                        setValue(5)
                                    }} role="button" className={
                                        value == 5 ?
                                        "card px-2 py-2 diklik"
                                        :
                                        "card px-2 py-2 notklik"
                                    } style={{borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
                                        Bea Keluar
                                </div>                                                                                                     
                                {
                                    arrKategoriBarang[pilihan].map((x)=>{
                                        return(
                                            <div onClick={()=>{
                                                setTab(x.object)
                                                setValue(x.value)
                                            }} role="button" className={
                                                value == x.value ?
                                                "card px-2 py-2 diklik"
                                                :
                                                "card px-2 py-2 notklik"
                                            } style={{borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
                                                {x.label}
                                            </div>                                                
                                        )
                                    })
                                }
                            </div>
                        </CCol>
                        <CCol md="9">
                            {tab}
                        </CCol>
                    </CRow>                    
                </div>
            </CModalBody>
            <CModalFooter>
                <div className="d-flex flex-row justify-content-end">
                    <div className="mr-2">
                        <CButton onClick={()=>{
                            setModal(false)
                            props.callback(false)
                        }} className="btn btn-success">Simpan</CButton>
                    </div>
                    <CButton onClick={()=>{
                        setModal(false)
                        props.callback(false)
                    }} className="btn btn-danger">Batal</CButton>
                </div>
            </CModalFooter>
        </CModal>
    )
}

export default ModalBarang