import {React} from 'react'
import { useDispatch, useSelector } from 'react-redux'
import {ReactComponent as Hello} from '../../../../assets/hello.svg'

const ModalOCRAwal = () =>{
    const dispatch = useDispatch()
    return(
        <div className="d-flex flex-row">
            <div className="ml-3 mr-3">
                <Hello/>
            </div>
            <div style={{width:'100%'}} className="d-flex flex-column px-5 py-5">
                <h2>Hi ! Selamat datang di layanan SSm Ekspor</h2>
                <h5 className="mt-3 mb-3">Yuk pilih metode pengisian data yang kamu inginkan</h5>
                <div className="d-flex flex-row">
                    <div onClick={()=>{
                        dispatch({type:'set', pageocr:2})
                    }} role="button" className="card px-3 py-3 d-flex flex-row mr-3 notklik" style={{borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
                        <div className="d-flex justify-content-center align-items-center mr-2">
                        <i class="fas fa-magic"></i>
                        </div>
                        <div className="d-flex justify-content-center align-items-center">
                            <b>Optical Character Recognition</b>
                        </div>                                
                    </div>
                    <div onClick={()=>{
                        dispatch({type: 'set', modal: false })
                    }} role="button" className="card px-3 py-3 d-flex flex-row notklik" style={{borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
                        <div className="d-flex justify-content-center align-items-center mr-2">
                        <i class="fas fa-pencil-alt"></i>
                        </div>
                        <div className="d-flex justify-content-center align-items-center">
                            <b>Pengisian Manual</b>
                        </div>                                
                    </div>                            
                </div>
            </div>
        </div>        
    )
}

export default ModalOCRAwal