import { CButton, CDataTable, CModalBody, CModalHeader, CModal, CModalFooter, CFormGroup, CLabel, CRow, CCol, CInput} from '@coreui/react'
import {React, useState} from 'react'

const DataKemasan = () =>{
    const [modal, setModal] = useState(false)
    return(
        <>
        <CModal
        show={modal}
        onClose={()=>setModal(false)}
        >
            <CModalHeader><h5>Edit Kemasan</h5></CModalHeader>
            <CModalBody>
                <CRow>
                    <CCol md="6">
                        <CFormGroup>
                            <CLabel>Kode Kemasan</CLabel>
                            <CInput value="DM" disabled/>
                        </CFormGroup>
                    </CCol>
                    <CCol md="6">
                        <CFormGroup>
                            <CLabel>Nama Kemasan</CLabel>
                            <CInput value="Crate, bulk, wooden" disabled/>
                        </CFormGroup>
                    </CCol>                    
                </CRow>
                <CRow>
                    <CCol md="6">
                        <CFormGroup>
                            <CLabel>Merk Kemasan</CLabel>
                            <CInput />
                        </CFormGroup>
                    </CCol>
                    <CCol md="6">
                        <CFormGroup>
                            <CLabel>Bahan Kemasan</CLabel>
                            <CInput />
                        </CFormGroup>
                    </CCol>                    
                </CRow> 
                <CRow>
                    <CCol md="12">
                        <CFormGroup>
                            <CLabel>Jumlah Kemasan</CLabel>
                            <CInput value="1000" disabled/>
                        </CFormGroup>
                    </CCol>                   
                </CRow>                               
            </CModalBody>
            <CModalFooter>
                <div className="d-flex flex-row justify-content-end">
                    <div className="mr-2">
                        <CButton onClick={()=>setModal(false)} className="btn btn-success">Simpan</CButton>
                    </div>
                    <div>
                        <CButton onClick={()=>setModal(false)} className="btn btn-danger">Batal</CButton>
                    </div>                    
                </div>
            </CModalFooter>
        </CModal>
        <CDataTable
        addTableClasses="joss"
        fields={[
            {label:'No',key:'no'},
            {label:'Kode Kemasan',key:'kdKemasan'},
            {label:'Nama Kemasan',key:'nmKemasan'},
            {label:'Merk Kemasan',key:'merkKemasan'},
            {label:'Bahan Kemasan',key:'bahanKemasan'},
            {label:'Jumlah',key:'jmlKemasan'},
            {label:'Aksi',key:'aksi'}
        ]}
        items={[
            {
                'kdKemasan':'DM',
                'nmKemasan':'Crate, bulk, wooden',
                'merkKemasan':'',
                'bahanKemasan':'',
                'jmlKemasan':'1000'
            }
        ]}
        scopedSlots={{
            'no':
            (item, index)=>{
                return(
                    <td>
                        {index+1}
                    </td>
                )
            },
            'aksi':
            (item, index)=>{
                return(
                    <td>
                        <div className="d-flex flex-row">
                            <div>
                                <CButton onClick={()=>setModal(true)} className="btn btn-sm btn-success"><i class="fas fa-pencil-alt"></i></CButton>
                            </div>                           
                        </div>
                    </td>
                )
            }
        }}
        />
        </>
    )
}

export default DataKemasan