import { CFormGroup, CInput, CLabel, CRow, CCol, CSelect, CTextarea, CButton } from '@coreui/react'
import {React, useEffect, useState} from 'react'
import { useDispatch, useSelector } from 'react-redux';
import AsyncSelect from 'react-select/async';
import Select from 'react-select'
import CreatableSelect from 'react-select/creatable';
import Swal from 'sweetalert2';
import supabase from '../../../../supabase'

const Perusahaan = () =>{
    const [disabled, setDisabled] = useState(false)
    const dispatch = useDispatch()
    const datasupabase = useSelector((state)=>state.datasupabase)
    const [disabledpgjawab, setDisabledpgjawab] = useState(true)
    const [disabledpembeli, setDisabledpembeli] = useState(false)
    const [perusahaan, setPerusahaan] = useState({})
    const kirimPerusahaan = async () =>{
        let sementara = {...datasupabase.jsondata}
        sementara.perusahaan = perusahaan
        const { data, error } = await supabase
        .from('td_superset')
        .update({ jsondata: sementara })
        .eq('id', datasupabase.id)  
        if(!error){
            Swal.fire({
                icon: 'success',
                title: 'Sukses'
            })  
            dispatch({type:'set', datasupabase:data[0]})
        }          
    }
    useEffect(()=>{
        setPerusahaan({...(datasupabase.jsondata ? datasupabase.jsondata.perusahaan : {})})
    },[])
    const handleCheck = (e) =>{
        setDisabledpembeli(e.target.checked)
        setPerusahaan({
            ...perusahaan,
            [e.target.name]:e.target.checked,
            ['nmpembeli']:"",
            ['negarapembeli']:"",
            ['alamatpembeli']:""            
        })        
    }
    const handleInputChange = (e) =>{
        if(e.target.name === 'jnsidentitaseksportir'){
            if(e.target.value !== "1"){
                setDisabled(false)
                setPerusahaan({
                    ...perusahaan,
                    [e.target.name]:e.target.value,
                    ['nibeksportir']:"",
                    ['nmperusahaaneksportir']:"",
                    ['rtrweksportir']:"",
                    ['kecamataneksportir']:"",
                    ['kotakabeksportir']:"",
                    ['kodeposeksportir']:"",
                    ['alamateksportir']:"",
                    ['teleponeksportir']:""
                })
            } else {
                setDisabled(true)
                setPerusahaan({...perusahaan,[e.target.name]:e.target.value})
            }          
        } else {
            setPerusahaan({...perusahaan,[e.target.name]:e.target.value})
        }
    }
    const arr = [
        {'value':"012337556641000", "label":"012337556641000"}
    ]
    const arrPgjawab = [
        {
            jabatanpgjawab:"KOMISARIS",
            notelppgjawab:"0816503995",
            alamatpgjawab:"DARMO PERMAI TIMUR 4/62"
        },
        {
            jabatanpgjawab:"DIREKTUR",
            notelppgjawab:"08123251959",
            alamatpgjawab:"PANDUGO BARU XII/M-15"
        },
        {
            jabatanpgjawab:"BADAN HUKUM",
            notelppgjawab:"0811325051",
            alamatpgjawab:"JL. RAYA CANDI NO. 5"
        },
        {
            jabatanpgjawab:"BADAN HUKUM",
            notelppgjawab:"08123011667",
            alamatpgjawab:"JL. RAYA CANDI NO. 5"
        }                        
    ]
    const handleSelect = (e, index) =>{
        if(index.name === "ideksportir"){
            setPerusahaan({
                ...perusahaan,
                [index.name]:e,
                ['nibeksportir']:"8120310100981",
                ['nmperusahaaneksportir']:"UNICHEMCANDI INDONESIA",
                ['rtrweksportir']:"-",
                ['kecamataneksportir']:"",
                ['kotakabeksportir']:"KAB. SIDOARJO",
                ['kodeposeksportir']:"61271",
                ['alamateksportir']:"Jl. Raya Candi No. 5",
                ['teleponeksportir']:"0318912342"
            })            
        } else if(index.name === "npwpppjk") {
            setPerusahaan({
                ...perusahaan,
                [index.name]:e,
                ['nmppjk']:"UNICHEMCANDI INDONESIA",
                ['alamatppjk']:"Jl. Raya Candi No. 5"
            })
        } else if (index.name === "namapgjawab"){
            if(e.value == "5"){
                setDisabledpgjawab(false)
                setPerusahaan({
                    ...perusahaan,
                    [index.name]:e,
                    ['nmpgjawab']:"",
                    ['jabatanpgjawab']:"",
                    ['notelppgjawab']:"",
                    ['alamatpgjawab']:""                
                })                 
            } else {
                setPerusahaan({
                    ...perusahaan,
                    [index.name]:e,
                    ['nmpgjawab']:e,
                    ['jabatanpgjawab']:arrPgjawab[parseInt(e.value)-1].jabatanpgjawab,
                    ['notelppgjawab']:arrPgjawab[parseInt(e.value)-1].notelppgjawab,
                    ['alamatpgjawab']:arrPgjawab[parseInt(e.value)-1].alamatpgjawab
                })                
            }
        }
    }
    const handleInputSelect = (e) =>{
        if(e.length > 5){
            return new Promise(resolve => {
                setTimeout(() => {
                  resolve(arr);
                }, 1000);
              });            
        }
    }
    return(
        <>
        <CRow>
            <CCol md="3">
                <CFormGroup>
                    <CLabel>Jenis Pemberitahu</CLabel>
                    <Select
                    defaultValue={{ label: "PENGUSAHA", value: '1' }}
                    options={[
                        {label:'PENGUSAHA',value:'1'},
                        {label:'PPJK',value:'2'},
                        {label:'VENDOR',value:'3'},
                    ]}
                    />
                </CFormGroup>
            </CCol>
        </CRow>
        <CRow>
            <CCol md="6">
                <h5>PENGUSAHA</h5>
                <hr/>
                <CRow>
                    <CCol md="6">
                        <CFormGroup>
                            <CLabel>NIB</CLabel>
                            <Select value={{'label':'9120100781919','value':'9120100781919'}} />
                        </CFormGroup>
                    </CCol>
                    <CCol md="6">
                        <CFormGroup>
                            <CLabel>NPWP</CLabel>
                            <CInput value="027681030529000" disabled/>
                        </CFormGroup>
                    </CCol>                    
                </CRow> 
                <CRow>
                    <CCol md="6">
                        <CFormGroup>
                            <CLabel>Nama Perusahaan</CLabel>
                            <CInput value="DASINDO" disabled/>
                        </CFormGroup>
                    </CCol>
                    <CCol md="6">
                        <CFormGroup>
                            <CLabel>Kota / Kabupaten</CLabel>
                            <CInput value="KAB. PURBALINGGA" disabled/>
                        </CFormGroup>
                    </CCol>                    
                </CRow>  
                <CRow>
                    <CCol md="6">
                        <CFormGroup>
                            <CLabel>Kecamatan</CLabel>
                            <CInput disabled/>
                        </CFormGroup>
                    </CCol>
                    <CCol md="6">
                        <CFormGroup>
                            <CLabel>Nomor Identitas BU/PU</CLabel>
                            <CInput value="100.2.1.04.21.0020" disabled/>
                        </CFormGroup>
                    </CCol>                    
                </CRow>
                <CRow>
                    <CCol md="3">
                        <CFormGroup>
                            <CLabel>RT/RW</CLabel>
                            <CInput value="RT01/RW" disabled/>
                        </CFormGroup>
                    </CCol>
                    <CCol md="3">
                        <CFormGroup>
                            <CLabel>Kode Pos</CLabel>
                            <CInput value="53362" disabled/>
                        </CFormGroup>
                    </CCol>                    
                    <CCol md="6">
                        <CFormGroup>
                            <CLabel>Alamat</CLabel>
                            <CTextarea value="Desa Gembong" disabled/>
                        </CFormGroup>
                    </CCol>                    
                </CRow> 
                <CRow>
                    <CCol md="6">
                        <CFormGroup>
                            <CLabel>Telepon</CLabel>
                            <CInput value="(0281) 659 7604" disabled/>
                        </CFormGroup>
                    </CCol>
                    <CCol md="6">
                        <CFormGroup>
                            <CLabel>Email,</CLabel>
                            <CInput value="cvdasindo@gmail.com" disabled/>
                        </CFormGroup>
                    </CCol>                    
                </CRow>                                                                                                                                                           
            </CCol>
            <CCol md="6">
                <h5>PENERIMA</h5>
                <hr/>
                <CRow>
                    <CCol md="6">
                        <CFormGroup>
                            <CLabel>Jenis Identitas</CLabel>
                            <CSelect/>
                        </CFormGroup>
                    </CCol>
                    <CCol md="6">
                        <CFormGroup>
                            <CLabel>Nomor Identitas</CLabel>
                            <CInput/>
                        </CFormGroup>
                    </CCol>                    
                </CRow>
                <CRow>
                    <CCol md="6">
                        <CFormGroup>
                            <CLabel>Nama Perusahaan</CLabel>
                            <CInput/>
                        </CFormGroup>
                    </CCol>
                    <CCol md="6">
                        <CFormGroup>
                            <CLabel>Negara</CLabel>
                            <Select/>
                        </CFormGroup>
                    </CCol>                    
                </CRow>  
                <CRow>
                    <CCol md="6">
                        <CFormGroup>
                            <CLabel>Kota/Kabupaten</CLabel>
                            <CInput disabled/>
                        </CFormGroup>
                    </CCol>
                    <CCol md="6">
                        <CFormGroup>
                            <CLabel>Kecamatan</CLabel>
                            <CInput disabled/>
                        </CFormGroup>
                    </CCol>                    
                </CRow> 
                <CRow>
                    <CCol md="3">
                        <CFormGroup>
                            <CLabel>RT / RW</CLabel>
                            <CInput disabled/>
                        </CFormGroup>
                    </CCol>
                    <CCol md="3">
                        <CFormGroup>
                            <CLabel>Kode Pos</CLabel>
                            <CInput disabled/>
                        </CFormGroup>
                    </CCol>                    
                    <CCol md="6">
                        <CFormGroup>
                            <CLabel>Alamat</CLabel>
                            <CTextarea/>
                        </CFormGroup>
                    </CCol>                    
                </CRow>
                <CRow>
                    <CCol md="6">
                        <CFormGroup>
                            <CLabel>Telepon</CLabel>
                            <CInput/>
                        </CFormGroup>
                    </CCol>
                    <CCol md="6">
                        <CFormGroup>
                            <CLabel>Email</CLabel>
                            <CInput/>
                        </CFormGroup>
                    </CCol>                    
                </CRow>                                                                                                                                                                                                       
            </CCol>                       
        </CRow>
        <hr/>
        <div className="d-flex flex-row justify-content-end">
            <div className="mr-2">
                <CButton onClick={()=>kirimPerusahaan()} style={{borderRadius:'12px'}} className="btn btn-success">Simpan</CButton>
            </div>           
        </div>
        </>
    )
}

export default Perusahaan