import {React, useState} from 'react'
import {CRow, CCol, CDataTable, CButton, CModalHeader, CModalBody, CModal, CFormGroup, CLabel, CInput, CModalFooter} from '@coreui/react'
import {ModalBarang} from './bridge'
import Select from 'react-select'
import DataKemasan from './datakemasan'

const Barang = () =>{
    const [modal, setModal] = useState(false)
    const [pilihan, setPilihan] = useState(1)
    const [modalkontainer, setModalKontainer] = useState(false)
    return(
        <>
        <CModal
        show={modalkontainer}
        size="xl"
        onClose={()=>setModalKontainer(false)}
        >
            <CModalHeader><h5>Tambah Kontainer</h5></CModalHeader>
            <CModalBody>
                <div className="card px-3 py-3" style={{borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
                    <CRow>
                        <CCol md="4">
                            <CFormGroup>
                                <CLabel>Nomor Kontainer</CLabel>
                                <CInput placeholder="Nomor Kontainer"/>
                            </CFormGroup>
                        </CCol>
                        <CCol md="4">
                            <CFormGroup>
                                <CLabel>Ukuran Kontainer</CLabel>
                                <Select
                                />
                            </CFormGroup>
                        </CCol>
                        <CCol md="4">
                            <CFormGroup>
                                <CLabel>Status / Stuffing Kontainer</CLabel>
                                <Select
                                options={[
                                    {label:'FCL', value:1},
                                    {label:'LCL', value:2}
                                ]}
                                />
                            </CFormGroup>
                        </CCol>                                                
                    </CRow>
                    <CRow>
                        <CCol md="4">
                            <CFormGroup>
                                <CLabel>Jumlah Kontainer</CLabel>
                                <CInput placeholder="Jumlah Kontainer"/>
                            </CFormGroup>
                        </CCol>
                        <CCol md="4">
                            <CFormGroup>
                                <CLabel>Nomor Seal</CLabel>
                                <CInput placeholder="Nomor Seal"/>
                            </CFormGroup>
                        </CCol>
                        <CCol md="4">
                            <CFormGroup>
                                <CLabel>Tipe Kontainer</CLabel>
                                <Select
                                options={[
                                    {label:'General/Dry Cargo Container', value:1},
                                    {label:'Tunnel Type Container', value:2},
                                    {label:'Open Top Steel Container', value:3},
                                    {label:'Flat Rack Container', value:4},
                                    {label:'Reefer/Refrigerated Container', value:5},
                                    {label:'Barge Container', value:6},
                                    {label:'Bulk Container', value:7},                                                                        
                                ]}
                                />
                            </CFormGroup>
                        </CCol>                                                
                    </CRow>                                        
                </div>
            </CModalBody>
            <CModalFooter>
                <div className="d-flex flex-row">
                    <div className="mr-2">
                        <CButton onClick={()=>setModalKontainer(false)} className="btn btn-success">Simpan</CButton>
                    </div>
                    <div>
                        <CButton onClick={()=>setModalKontainer(false)} className="btn btn-danger">Batal</CButton>
                    </div>                    
                </div>
            </CModalFooter>
        </CModal>
        <ModalBarang modal={modal} callback={(x)=>{
            setModal(x)
        }}/>
        <div className="d-flex flex-row">
            <div className={
                pilihan == 1 ?
                "card px-2 py-2 diklik mr-2"
                :
                "card px-2 py-2 notklik mr-2"                
            } onClick={()=>setPilihan(1)} role="button" style={{borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
                Data Barang
            </div>
            <div className={
                pilihan == 2 ?
                "card px-2 py-2 diklik mr-2"
                :
                "card px-2 py-2 notklik mr-2"                
            } onClick={()=>setPilihan(2)} role="button" style={{borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
                Data Kontainer
            </div>
            <div className={
                pilihan == 3 ?
                "card px-2 py-2 diklik mr-2"
                :
                "card px-2 py-2 notklik mr-2"                
            } onClick={()=>setPilihan(3)} role="button" style={{borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
                Data Kemasan
            </div>                        
        </div>
        {
            pilihan == 1 ?
            <>
            <div className="card px-3 py-3" style={{borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
                <CRow>
                    <CCol md="12">
                        <div className="d-flex flex-row justify-content-start mb-2">
                            <div className="mr-2">
                                <CButton 
                                // onClick={()=>setModal(true)} 
                                className="btn btn-sm" style={{backgroundColor:'#02275d', color:'#fff', borderRadius:'12px'}}><i class="fas fa-plus"></i></CButton>                                
                            </div>
                            <div className="mr-2">
                                <CButton 
                                // onClick={()=>setModal(true)} 
                                className="btn btn-sm" style={{backgroundColor:'#02275d', color:'#fff', borderRadius:'12px'}}><i class="fas fa-cloud-download-alt"></i></CButton>                                
                            </div>
                            <div className="mr-2">
                                <CButton 
                                // onClick={()=>setModal(true)} 
                                className="btn btn-sm" style={{backgroundColor:'#02275d', color:'#fff', borderRadius:'12px'}}><i class="fas fa-cloud-upload-alt"></i></CButton>                                
                            </div>                                                        
                        </div>
                        <CDataTable
                        addTableClasses="joss"
                        fields={[
                            {label:'No', key:'nomor'},
                            {label:'Kode HS', key:'hscode'},
                            {label:'Uraian Barang', key:'uraian'},
                            {label:'Kode Barang', key:'uraian'},
                            {label:'Jumlah Satuan', key:'uraian'},
                            {label:'Kode Satuan', key:'uraian'},
                            {label:'Negara', key:'negAsal'},
                            {label:'Action', key:'action'}
                        ]}
                        />
                    </CCol>
                </CRow>                  
            </div>          
            </>     
            :
            pilihan == 2 ?
            <>
            <div className="card px-3 py-3" style={{borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
                <CRow>
                    <CCol md="12">
                        <div className="d-flex flex-row justify-content-start mb-2">
                            <div className="mr-2">
                                <CButton 
                                // onClick={()=>setModal(true)} 
                                className="btn btn-sm" style={{backgroundColor:'#02275d', color:'#fff', borderRadius:'12px'}}><i class="fas fa-plus"></i></CButton>                                
                            </div>
                            <div className="mr-2">
                                <CButton 
                                // onClick={()=>setModal(true)} 
                                className="btn btn-sm" style={{backgroundColor:'#02275d', color:'#fff', borderRadius:'12px'}}><i class="fas fa-cloud-download-alt"></i></CButton>                                
                            </div>
                            <div className="mr-2">
                                <CButton 
                                // onClick={()=>setModal(true)} 
                                className="btn btn-sm" style={{backgroundColor:'#02275d', color:'#fff', borderRadius:'12px'}}><i class="fas fa-cloud-upload-alt"></i></CButton>                                
                            </div>                            
                        </div>
                        <CDataTable
                        addTableClasses="joss"
                        fields={[
                            {key:'no',label:'Nomor'},
                            {key:'noCont',label:'Nomor Kontainer'},
                            {key:'noCont',label:'Jenis Seal'},
                            {key:'noCont',label:'Nomor Seal'},
                            {key:'ukCont',label:'Tipe Kontainer'},
                            {key:'ukCont',label:'Ukuran Kontainer'},
                            {key:'statusCont',label:'Stuffing'},
                            {key:'action',label:'Action'},
                        ]}
                        />
                    </CCol>
                </CRow>                 
            </div>           
            </>
            :
            <>
            <div className="card px-3 py-3" style={{borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
                <DataKemasan/>                
            </div>                        
            </>       
        }
        {/* <div className="card px-3 py-3" style={{borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
            <CRow>
                <CCol md="4">
                    <CFormGroup>
                        <CLabel>Nilai</CLabel>
                        <CInput disabled/>
                    </CFormGroup>
                </CCol>                
                <CCol md="3">
                    <CFormGroup>
                        <CLabel>Bruto</CLabel>
                        <CInput disabled/>
                    </CFormGroup>
                </CCol>
                <CCol md="1">
                    <CFormGroup>
                        <CLabel>Satuan</CLabel>
                        <CInput disabled/>
                    </CFormGroup>
                </CCol>  
                <CCol md="3">
                    <CFormGroup>
                        <CLabel>Netto</CLabel>
                        <CInput disabled/>
                    </CFormGroup>
                </CCol>
                <CCol md="1">
                    <CFormGroup>
                        <CLabel>Satuan</CLabel>
                        <CInput disabled/>
                    </CFormGroup>
                </CCol>                                
            </CRow>                        
        </div>         */}
        </>
    )
}

export default Barang