import {React, useState} from 'react'
import ListLayanan from './listlayanan'
import AjuBarantan from './ajubarantan'
import { CDataTable, CButton} from '@coreui/react'

const DataLayanan = () =>{
    const [page, setPage] = useState(0)
    return(
        <>
        <CDataTable
        addTableClasses='joss'
        fields={[
            {label:'Layanan', key:'1'},
            {label:'Nomor Pengajuan', key:'2'},
            {label:'Nomor Pendaftaran', key:'3'},
            {label:'Tanggal Pendaftaran', key:'4'},
            {label:'Status', key:'5'},
            {label:'Action', key:'6'},
        ]}
        items={[
            {
                1:'BC 3.0',
                2:'',
                3:'',
                4:'',
                5:'Belum Mengajukan'
            },
            {
                1:'Karantina Barantan',
                2:'',
                3:'',
                4:'',
                5:'Belum Mengajukan'
            }, 
            {
                1:'Karantina BKIPM',
                2:'',
                3:'',
                4:'',
                5:'Belum Mengajukan'
            },                       
            {
                1:'SKA',
                2:'',
                3:'',
                4:'',
                5:'Belum Mengajukan'
            }            
        ]}
        scopedSlots={{
            '6':
            (item, index)=>{
                return(
                    <td>
                        <div className="d-flex flex-row justify-content-start mb-2">
                            <div className="mr-2">
                                <CButton 
                                // onClick={()=>setModal(true)} 
                                className="btn btn-sm" style={{backgroundColor:'#02275d', color:'#fff', borderRadius:'12px'}}><i class="fas fa-arrow-right"></i></CButton>                                
                            </div>
                            <div className="mr-2">
                                <CButton 
                                // onClick={()=>setModal(true)} 
                                className="btn btn-sm" style={{backgroundColor:'#02275d', color:'#fff', borderRadius:'12px'}}><i class="fas fa-pencil-alt"></i></CButton>                                
                            </div>
                            <div className="mr-2">
                                <CButton 
                                // onClick={()=>setModal(true)} 
                                className="btn btn-sm" style={{backgroundColor:'#f86c6b', color:'#fff', borderRadius:'12px'}}><i class="fas fa-ban"></i></CButton>                                
                            </div>                                                        
                        </div>                        
                    </td>
                )
            }
        }}
        />
        </>
    )
}

export default DataLayanan