import { CFormGroup, CLabel, CCol, CInput, CRow} from '@coreui/react'
import {React} from 'react'
import Select from 'react-select'

const DataTumbuhan = () =>{
    return(
        <div className="card px-3 py-3" style={{borderRadius:'12px', boxShadow:'rgb(0 0 0 / 10%) 0px 2px 4px, rgb(0 0 0 / 10%) 0px 8px 16px'}}>
            <h5>Data Tumbuhan</h5>
            <hr/>
            <CRow>
                <CCol md="6">
                    <CFormGroup>
                        <CLabel>Jenis</CLabel>
                        <Select
                        options={[
                            {label:'Benih', value:'1'},
                            {label:'Non Benih', value:'2'}
                        ]}                        
                        />
                    </CFormGroup>
                </CCol>
                <CCol md="6">
                    <CFormGroup>
                        <CLabel>Bentuk</CLabel>
                        <Select                       
                        />
                    </CFormGroup>
                </CCol>                                            
            </CRow>
            <CRow>
                <CCol md="6">
                    <CFormGroup>
                        <CLabel>Peruntukan</CLabel>
                        <CInput disabled placeholder="Please Select Bentuk"/>
                    </CFormGroup>
                </CCol>   
                <CCol md="6">
                    <CFormGroup>
                        <CLabel>Komoditi</CLabel>
                        <Select                       
                        />
                    </CFormGroup>
                </CCol>                  
            </CRow>
            <CRow>
                <CCol md="6">
                    <CFormGroup>
                        <CLabel>Nama Umum</CLabel>
                        <CInput placeholder="Nama Umum"/>
                    </CFormGroup>
                </CCol>   
                <CCol md="6">
                    <CFormGroup>
                        <CLabel>Nama Latin</CLabel>
                        <CInput placeholder="Nama Latin"/>                        
                    </CFormGroup>
                </CCol>                  
            </CRow>            
        </div>
    )
}

export default DataTumbuhan